const filtrProjects = () => {
    const postTypeArchiveProjects = document.querySelector('.post-type-archive-projects')
    if ( postTypeArchiveProjects ) {
        const buttons = document.querySelectorAll('.section-filtr__items button')
        const featuredContent = document.querySelector('.featured__content')
        const extrasContent = document.querySelector('.extras__content')
        buttons.forEach((item) => {
            item.addEventListener('click', (e) => {
                btnActive(buttons);
                item.classList.add('active')
                var termId = item.id;
                (function($) {
                    $.ajax({
                        url: "/wp-admin/admin-ajax.php",
                        method: 'post',
                        data: {
                            action: 'axiomasoft',
                            //nonce: nonce.value,
                            term: termId
                        },
                        dataType: "json",
                        //async: false,
                        success: function (data) {
                            var grid = data.grid;
                            var grid_json = JSON.parse(data.grid);
                            var list = data.list;
                            var list_json = JSON.parse(data.list);
                            featuredContent.innerHTML = grid_json
                            extrasContent.innerHTML = list_json
                        },
                        error: function (jqXHR, text, error) {}
                    });
                })( jQuery );
            })
        });
    }
}
filtrProjects()

function btnActive(buttons) {
    buttons.forEach((item) => {
        item.classList.remove('active')
    });
}
