const reviewsSwiper = () => {
    const reviewsSwiperSelect = document.querySelector('.reviewsSwiper')
    if ( reviewsSwiperSelect ) {
        var swiper = new Swiper(reviewsSwiperSelect, {
            slidesPerView: 1,
            // centeredSlidesBounds: false,
            // centeredSlides: true,
            spaceBetween: 20,
            // slidesOffsetAfter: 20,
            // slidesOffsetBefore: 20,
            grabCursor: true,
            breakpoints: {
                // 320: {
                //     slidesPerView: 2,
                //     spaceBetween: 20
                // },
                // 529: {
                //     slidesPerView: 2,
                //     centeredSlidesBounds: false,
                // },
                529: {
                    slidesPerView: "auto",
                    centeredSlidesBounds: true,
                    slidesOffsetAfter: 20,
                    slidesOffsetBefore: 20,
                }
            },
            pagination: {
                el: ".swiper-pagination",
                clickable: true,
            },
        });
    }
}
reviewsSwiper()
