const tabsArchive = () => {
    const postTypeArchiveProjects = document.querySelector('.post-type-archive-projects')
    if ( postTypeArchiveProjects ) {
        const list = document.querySelector('#list')
        const grid = document.querySelector('#grid')
        const projectsList = document.querySelector('#projects-list')
        const projectsGrid = document.querySelector('#projects-grid')
        list.addEventListener('click', (e) => {
            grid.classList.remove('active')
            projectsGrid.classList.remove('active')
            list.classList.add('active')
            projectsList.classList.add('active')
        })
        grid.addEventListener('click', (e) => {
            list.classList.remove('active')
            projectsList.classList.remove('active')
            grid.classList.add('active')
            projectsGrid.classList.add('active')
        })
    }
}
tabsArchive()
